.show-more-btn-cont {
    display: flex;
    justify-content: center;
}

.show-more-btn {
    align-items: center;
    border: 1px solid rgb(255, 199, 0);
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    height: 50px;
    justify-content: center;
    padding: 0px 20px;
    width: 100px;
}

.show-more-btn:hover {
    transform: scale(1.1)
}